@import url('https://fonts.googleapis.com/css2?family=Belanosima&family=Signika:wght@300;400&family=Ubuntu:wght@500&display=swap');

.achieve{
    margin-top:5rem;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    text-align: center;
    margin-bottom:10rem;
}

.achieve h1{
    text-transform:uppercase;
    font-family:'Signika', sans-serif;

}

.list {
    margin-top: 3rem;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  .list p {
    margin: 0;
    width: 47%;
    text-align: left;
    font-size:1.3rem;
    line-height: 2;
    color:rgb(92, 88, 88);
    font-weight: 500;
    
  }

  @media(max-width:1300px){
    .list p {
      width: 80%;
      
    }

  }

  @media(max-width:400px){
    .list p {
      width: 90%;
      
    }

  }




  
  